"use server";

import prisma from "../db/db";

// Bring all users
export async function getAllUsers() {
  const users = await prisma.user.findMany();

  return users;
}

// Bring specific user
export async function getUserProfile(userId: number) {
  const userProfile = await prisma.user.findUnique({
    where: { id: userId },
  });

  return userProfile;
}
